            time: 1500
        }
        Alerts.showInfoAlert(alert)
    }

    maintenanceArea() {
        this.sendGcode('G1 x500 y0 f6000')
        const alert: InfoAlertType = {
            message: '    ',
            type: 'green',
            time: 1500
        }
        Alerts.showInfoAlert(alert)
    }

